<template>
    <div class="column is-multiline">
      <div class="column is-6 is-offset-3">
        <div class="box">
          <form @submit.prevent="onSubmit">
            <div class="field">
              <label class="label">Add Your Todos</label>
              <div class="control">
                <input class="input" type="text" placeholder="Enter Your todo..." v-model="title" />
              </div>
            </div>
          </form>
        </div>
      </div>
      <div class="column is-6 is-offset-3" v-for="todo in todos" :key="todo.id">
        <div class="box todo_box" @click="$emit('onComplete', todo.id)">
          <span :class="{ completed: todo.completed }">{{ todo.title }}</span>
          <div class="date-time">
            <small class="has-text-grey">{{ formatDate(todo.date) }}</small>
          </div>
          <button class="button is-danger is-small is-pulled-right" v-if="todo.completed" @click="$emit('onDelete', todo.id)">
            Delete
          </button>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import shortid from 'shortid';
  
  export default {
    name: "TodoList",
    props: ["todos"],
    data() {
      return {
        title: ""
      };
    },
    methods: {
      onSubmit() {
        const new_todo = {
          title: this.title,
          completed: false,
          id: shortid.generate(),
          date: new Date(),
        };
        this.$emit("addTodo", new_todo);
        this.title = "";
      },
      formatDate(date) {
        const options = {
          year: 'numeric',
          month: 'long',
          day: 'numeric',
          hour: 'numeric',
          minute: 'numeric',
          hour12: true,
        };
  
        return new Intl.DateTimeFormat('en-US', options).format(date);
      },
    }
  };
  </script>
  
  <style>
  .completed {
    text-decoration: line-through;
  }
  
  .todo_box {
    transition: all 0.3s;
    position: relative;
  }
  
  .todo_box:hover {
    cursor: pointer;
    background-color: #f0f3bd;
    transform: scale(1.1);
  }
  
  .date-time {
    margin-top: 5px;
  }
  
  /* Align delete button to the right */
  .button.is-danger.is-small {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 10px;
  }
  
  /* Darken text color */
  .has-text-grey {
    color: #444;
  }
  </style>
  