<template>
  <div id="app">
    <div class="section">
      <div class ="container">
    <TodoList :todos="todos" @addTodo = "addTodo" @onComplete="completed" @onDelete= "deleted" />
      </div>
    </div>
  </div>
  </template>
  
  <script>
  import "bulma/css/bulma.css";
import TodoList from "./components/Todos";
  export default {
    name: 'App',
    components: {
      TodoList
    },
    data(){
      return {
        todos:[ {
      "userId": 1,
      "id": 1,
      "title": "delectus aut autem",
      "completed": false
    },
    {
      "userId": 1,
      "id": 2,
      "title": "quis ut nam facilis et officia qui",
      "completed": false
    },
    {
      "userId": 1,
      "id": 3,
      "title": "fugiat veniam minus",
      "completed": false
    },
    {
      "userId": 1,
      "id": 4,
      "title": "et porro tempora",
      "completed": true
    },
    {
      "userId": 1,
      "id": 5,
      "title": "laboriosam mollitia et enim quasi adipisci quia provident illum",
      "completed": false
    },
    {
      "userId": 1,
      "id": 6,
      "title": "qui ullam ratione quibusdam voluptatem quia omnis",
      "completed": false
    },
    {
      "userId": 1,
      "id": 7,
      "title": "illo expedita consequatur quia in",
      "completed": false
    },
    {
      "userId": 1,
      "id": 8,
      "title": "quo adipisci enim quam ut ab",
      "completed": true
    },]
  };
    },
      methods: {
        addTodo(todo) {
          const options = { year: 'numeric', month: 'long', day: 'numeric', hour: 'numeric', minute: 'numeric', second: 'numeric' };
          todo.date = new Date().toLocaleString(undefined, options);
          this.todos.unshift(todo);
        },
        completed(id) {
        const updated_todos = this.todos.map((todo) =>todo.id == id ? Object.assign(todo, {completed:true}):todo);
          this.todos = updated_todos;
        },
        deleted(id) {
         let deleted_todos= this.todos.filter(todo => todo.id != id);
         this.todos = deleted_todos;
        },
      },
      
    
  };
  </script>
  
  <style>
  #app {
    background-color: #7e96eb;
    font-family: "Roboto";
    min-block-size: 100vh;
  }
